import React from 'react'
import { NavLink } from 'react-router-dom'
import '../styles/NavBar.css'

const NavBar = () => {
    return (
        <div className="navbar">
            <NavLink activeClassName='active-link' to="/insertion">Insertion</NavLink>
            <NavLink activeClassName='active-link' to="/bubble">Bubble</NavLink>
            <NavLink activeClassName='active-link' to="/selection">Selection</NavLink>
            <NavLink activeClassName='active-link' to="/merge">Merge</NavLink>
        </div>
    )
}

export default NavBar
